import React, {useState, useEffect} from 'react'
import { getUserInfo, getHeader } from '../../helpers/Auth';
import axios from "axios";

const Profile = (props) => {
    const [user, setUser] = useState({});
    const [name, setName] = useState();
    const [address, setAddress] = useState();
    const [phone, setPhone] = useState();

    useEffect(() => {
        getUserInfo().then(res => {
            setUser(res.data);
            setName(res.data.info.name);
            setAddress(res.data.info.address);
            setPhone(res.data.info.phone);
          });
        return;
    },[])

    const onNameChange = (e) => {
      setName(e.target.value);
    }

    const onAddressChange = (e) => {
      setAddress(e.target.value);
    }
    const onPhoneChange = (e)=>{
      setPhone(e.target.value);
    }

    const postEdit = e => {
    e.preventDefault();
    let url = process.config.apiUrl + "/token/edit";
    let body = { name , address, phone};
    console.log(url, body);
    axios
      .post(url, body, {   headers: getHeader() })
      .then(res => {
        console.log( res);
        if (res.data.ok === 1)
          window.$.alert({
            title: "Thnh cng",
            content:
              res.data.message || "Thm phng thc thanh ton thnh cng",
            type: "green",
            animationSpeed: 100
          });
        else {
          window.$.alert({
            title: "Tht bi",
            content: res.data.message || "C li xy ra, vui lng th li sau!",
            type: "red",
            animationSpeed: 100
          });
        }
      });
  };

  return (
    <section className="content">
      <div className="box box-primary">admin/category/list
        <div className="box-header with-border">
          <h3 className="box-title">Thm danh mc</h3>
        </div>
        {/* <!-- /.box-header --> */}
        {/* <!-- form start --> */}
        <form role="form">
          <div className="box-body">

            <div className="form-group">
            <label for="inputName">Tn</label>
              <input
                required
                type="text"
                className="form-control"
                id="inputName"
                name="name"
                placeholder="Nhp tn"
                value={name}
                onChange={e => onNameChange(e)}
              />
            </div>

            <div className="form-group">
              <label for="inputAddress">a ch</label>
              <input
                required
                type="text"
                className="form-control"
                id="inputAddress"
                address="address"
                placeholder="Nhp tn"
                value={address}
                onChange={(e) => onAddressChange(e)}
              />
            </div>
            <div className="form-group">
              <label for="inputPhone">S in thoi</label>
              <input
                required
                type="text"
                className="form-control"
                id="inputPhone"
                phone="phone"
                placeholder=""
                value={phone}
                onChange={(e) => onPhoneChange}
                
              />
            </div>

            <div className="form-group">
              <label for="inputEmail">Email</label>
              <input
                disabled
                required
                type="text"
                className="form-control"
                id="inputEmail"
                email="email"
                placeholder="Nhp tn"
                value={user.info ? user.email :  ""}
                />
                </div>
            
          </div>
          {/* <!-- /.box-body --> */}

          <div className="box-footer">
            <button type="submit" onClick={postEdit} className="btn btn-primary">
              Sa
            </button>
          </div>
        </form>
      </div>
    </section>
  )
}

export default Profile
 